import { FocusEvent, useCallback, useMemo } from 'react';

import { getFormattedDateValue } from '@packages/helpers';
import { DateRangePickerInputViewProps } from 'components/views/DateRangePickerInputView';
import { useIsRequiredInput } from 'hooks/useIsRequiredInput';
import { DEFAULT_SAVE_FORMAT } from 'inputs/DateRangePickerInput/constants';
import getDateFromRangeValue from 'inputs/DateRangePickerInput/helpers/getDateFromRangeValue';
import { get } from 'lodash';
import { useController, UseControllerProps, useWatch } from 'react-hook-form';

export type UseDateRangePickerInputProps = Pick<UseControllerProps, 'defaultValue'> &
  Pick<DateRangePickerInputViewProps, 'required' | 'onAccept'> & {
    fromName: string;
    toName: string;
    saveFormat?: string;
    onBlur?: (e: FocusEvent<HTMLInputElement>) => void;
  };

export interface UseDateRangePickerInputReturn
  extends Pick<DateRangePickerInputViewProps, 'value' | 'required' | 'onAccept'> {
  error: string | null;
  onBlur: UseDateRangePickerInputProps['onBlur'];
}

export const useDateRangePickerInput = (
  props: UseDateRangePickerInputProps,
): UseDateRangePickerInputReturn => {
  const {
    fromName,
    toName,
    saveFormat = DEFAULT_SAVE_FORMAT,
    defaultValue,
    required: requiredFromProps,
    onBlur,
    onAccept,
  } = props;

  const fromDefaultValue = get(defaultValue, fromName);
  const toDefaultValue = get(defaultValue, toName);

  const fromControllerProps = { ...props, name: fromName, defaultValue: fromDefaultValue };
  const toControllerProps = { ...props, name: toName, defaultValue: toDefaultValue };

  const {
    field: { onChange: onFromChange, onBlur: onFromBlur },
    fieldState: { error: fromError },
  } = useController(fromControllerProps);

  const {
    field: { onChange: onToChange, onBlur: onToBlur },
    fieldState: { error: toError },
  } = useController(toControllerProps);

  const [fromValue, toValue] = useWatch({
    name: [fromName, toName],
  });

  const isFromInputRequired = useIsRequiredInput(fromControllerProps);
  const isToInputRequired = useIsRequiredInput(toControllerProps);

  const isRequired = isFromInputRequired || isToInputRequired || requiredFromProps;

  const handleValueChange = useCallback<NonNullable<DateRangePickerInputViewProps['onAccept']>>(
    (newValue) => {
      const { startDate, endDate } = newValue;
      const formattedFromValue = getFormattedDateValue({
        value: startDate,
        format: saveFormat,
        fallbackValue: '',
      });
      const formattedToValue = getFormattedDateValue({
        value: endDate,
        format: saveFormat,
        fallbackValue: '',
      });

      onFromChange(formattedFromValue);
      onToChange(formattedToValue);
      onAccept?.(newValue);
    },
    [onAccept, onFromChange, onToChange, saveFormat],
  );

  const handleInputBlur = useCallback(
    (e: FocusEvent<HTMLInputElement>) => {
      // we do blur to make inputs touched for showing validation errors
      onFromBlur();
      onToBlur();
      onBlur?.(e);
    },
    [onBlur, onFromBlur, onToBlur],
  );

  const value = useMemo<DateRangePickerInputViewProps['value']>(
    () => ({
      startDate: getDateFromRangeValue({ value: fromValue }),
      endDate: getDateFromRangeValue({ value: toValue }),
    }),
    [fromValue, toValue],
  );

  const fromErrorMessage = fromError?.message;
  const toErrorMessage = toError?.message;
  const error = (fromErrorMessage || toErrorMessage) ?? null;

  return {
    value,
    error,
    required: isRequired,
    onAccept: handleValueChange,
    onBlur: handleInputBlur,
  };
};
