import { getFormattedDateRange } from 'helpers/getFormattedDateRange';

import { GetFormattedToolbarLabelParams, GetFormattedToolbarLabelReturn } from './types';

const getFormattedToolbarLabel = (
  params: GetFormattedToolbarLabelParams,
): GetFormattedToolbarLabelReturn => {
  const { startDate, endDate, separator, format } = params;

  const label = getFormattedDateRange({
    startDate,
    endDate,
    separator,
    dateDisplayFormat: format,
  });

  // MUI like
  return label ? label : '--';
};

export default getFormattedToolbarLabel;
