import { FC, PropsWithChildren } from 'react';

import { Box, Divider, generateUtilityClasses } from '@mui/material';
import { Typography } from 'components/Typography';
import { styled } from 'theme/index';

interface DropdownGroupProps {
  title: string;
}

const PREFIX = 'AutocompleteDropdownGroup';

export const AutocompleteDropdownGroupClasses = generateUtilityClasses(PREFIX, [
  'root',
  'titleWrapper',
  'title',
  'childrenWrapper',
  'divider',
]);

const StyledRoot = styled(Box, {
  name: PREFIX,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`&.${AutocompleteDropdownGroupClasses.root}`]: {
      '&:last-child': {
        [`& > .${AutocompleteDropdownGroupClasses.divider}`]: {
          display: 'none',
        },
      },
    },
    [`& .${AutocompleteDropdownGroupClasses.titleWrapper}`]: {
      padding: theme.spacing(1, 2),
    },

    [`& .${AutocompleteDropdownGroupClasses.divider}`]: {
      margin: theme.spacing(1, 0),
    },
  };
});

/**
 * It works good with our AutocompleteInput styles only. So the component is in folder of AutocompleteInputView.
 * Mui Select has no groups. With default MUI Autocomplete it has other view.
 */
export const DropdownGroup: FC<PropsWithChildren<DropdownGroupProps>> = (props) => {
  const { title, children } = props;

  return (
    <StyledRoot component="li" className={AutocompleteDropdownGroupClasses.root}>
      {title && (
        <Box className={AutocompleteDropdownGroupClasses.titleWrapper}>
          <Typography
            variant="body2"
            color="text.primary"
            className={AutocompleteDropdownGroupClasses.title}
          >
            {title}
          </Typography>
        </Box>
      )}
      <Box className={AutocompleteDropdownGroupClasses.childrenWrapper}>{children}</Box>
      <Divider className={AutocompleteDropdownGroupClasses.divider} />
    </StyledRoot>
  );
};
