import { getIsConvertableToDate } from '@packages/helpers';
import { addMinutes, isDate } from 'date-fns';

import { GetDateFromRangeValueParams, GetDateFromRangeValueReturn } from './types';

const getDateFromRangeValue = (
  params: GetDateFromRangeValueParams,
): GetDateFromRangeValueReturn => {
  const { value } = params;

  const isConvertableToDate = getIsConvertableToDate(value);

  if (!value || !isConvertableToDate) return undefined;

  if (isDate(value)) {
    /** It's important to return the same Date instance we receive
     * because we want to show an empty string after the separator
     * in the text field if the user selects only the start date,
     * but the react-date-range lib passes the same Date instance
     * in the end date instead of undefined or null, so we compare
     * Date objects to check whether they are the same. */
    return value as Date;
  }

  const dateWithUserTimezone = new Date(value as string);
  const timezoneOffset = dateWithUserTimezone.getTimezoneOffset();
  return addMinutes(dateWithUserTimezone, timezoneOffset);
};

export default getDateFromRangeValue;
