import { ReactNode, useCallback, useEffect, useMemo, useState } from 'react';

import { createPortal } from 'react-dom';

import { Box, useMediaQuery, useTheme } from '@mui/material';
import { clsx } from 'clsx';
import { useDateFnsLocale } from 'hooks/useDateFnsLocale';
import { isEqual, noop, set } from 'lodash';
import {
  AriaLabelsShape,
  DateRange as LibDateRange,
  DateRangePicker as LibDateRangePicker,
  DateRangePickerProps as LibDateRangePickerProps,
} from 'react-date-range';

import { MAIN_RANGE_KEY } from './constants';
import { getFocusedRange } from './helpers/getFocusedRange';
import { getLoadingMonthsIndexes } from './helpers/getLoadingMonthsIndexes';
import getSelectionRange from './helpers/getSelectionRange';
import StyledRoot, { PickerClasses, PickerLibClasses } from './StyledPicker';
import { PickerProps } from './types';

import { MonthLoader } from '../MonthLoader';
import { Navigation } from '../Navigation';
import { PickerDay } from '../PickerDay';

export const Picker = <
  DaySlot extends typeof PickerDay = typeof PickerDay,
  NavigationSlot extends typeof Navigation = typeof Navigation,
  MonthLoaderSlot extends typeof MonthLoader = typeof MonthLoader,
>(
  props: PickerProps<DaySlot, NavigationSlot, MonthLoaderSlot>,
): ReactNode => {
  const {
    value,
    focusedDateBehavior,
    dayDisplayFormat,
    direction,
    color,
    ariaLabels,
    loadingRanges = [],
    months = 2,
    locale,
    sx,
    className,
    slots,
    slotProps,
    onShownDateChange,
    onChange,
    ...rest
  } = props;

  const { startDate, endDate } = value;

  const {
    day: DaySlot = PickerDay,
    navigation: NavigationSlot = Navigation,
    monthLoader: MonthLoaderSlot = MonthLoader,
  } = slots || {};

  const {
    day: daySlotProps,
    navigation: navigationSlotProps,
    monthLoader: monthLoaderSlotProps,
  } = slotProps || {};

  const [rootNode, setRootNode] = useState<HTMLDivElement | null>(null);
  const [monthsNodes, setMonthsNodes] = useState<Array<Element>>([]);

  const [shownDate, setShownDate] = useState<Date>(() => {
    return startDate || new Date();
  });

  const dateFnsLocale = useDateFnsLocale();

  const finalLocale = locale ?? dateFnsLocale;

  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  const isDirectionPassed = direction !== undefined;

  const responsiveDirection: NonNullable<LibDateRangePickerProps['direction']> = isMobile
    ? 'vertical'
    : 'horizontal';

  const finalDirection = isDirectionPassed ? direction : responsiveDirection;

  const focusedRange = getFocusedRange({ startDate, endDate, focusedDateBehavior });

  const customColor = color || theme.palette.primary.light;

  const selectionRange = getSelectionRange({
    startDate,
    endDate,
    key: MAIN_RANGE_KEY,
  });

  const handleSelect = useCallback<NonNullable<LibDateRangePickerProps['onChange']>>(
    (params) => {
      const { startDate: newStartDate, endDate: newEndDate } = params[MAIN_RANGE_KEY] || {};

      /**
       * When the end date is not selected, `react-date-range` returns the same instance
       * of `Date` object as a start date, however, when the end date is equal to the start date
       * the instances will be different, even with the same date they represent
       */
      const isEndDateSelected = newStartDate !== newEndDate;

      onChange({
        startDate: newStartDate,
        endDate: isEndDateSelected ? newEndDate : undefined,
      });
    },
    [onChange],
  );

  const navigatorRenderer: NonNullable<LibDateRangePickerProps['navigatorRenderer']> = useCallback(
    (currentMonthDate, changeShownDate, calendarProps) => {
      /**
       * We need to have an actual current shown date in the state,
       * so we set it from the `navigatorRenderer` because
       * `onShownDateChange` works only when user manually
       * switches months. We do it in timeout in order to
       * not to do a state update in the render phase of the
       * lib calendar component.
       */
      if (!isEqual(shownDate, currentMonthDate)) {
        const timer = setTimeout(() => {
          clearTimeout(timer);
          setShownDate(currentMonthDate);
        });
      }

      return (
        <NavigationSlot
          currentFocusedDate={currentMonthDate}
          changeShownDate={changeShownDate}
          calendarProps={calendarProps}
          {...navigationSlotProps}
        />
      );
    },
    [shownDate, NavigationSlot, navigationSlotProps],
  );

  const dayContentRenderer: NonNullable<LibDateRangePickerProps['dayContentRenderer']> =
    useCallback(
      (date) => {
        return <DaySlot date={date} format={dayDisplayFormat} {...daySlotProps} />;
      },
      [dayDisplayFormat, DaySlot, daySlotProps],
    );

  /**
   * @see https://github.com/hypeserver/react-date-range/issues/529
   * @see https://github.com/hypeserver/react-date-range/issues/505
   * Whenever any prop is changed, the `Calendar` component
   * will jump to the initial selected/focused date. Seems like
   * `shownDate` prop doesn't do anything also.
   * Workaround: since the `Calendar` is a class component we can get the
   * instance from the ref and make `updateShownDate` do not do
   * anything, so we will prevent selected months state resetting.
   */
  const disableSelectedMonthsResetOnPropsChange = useCallback(
    (instance: LibDateRange | LibDateRangePicker | null) => {
      if (!instance) return;

      set(instance, 'calendar.updateShownDate', noop);
    },
    [],
  );

  const updateMonthsNodes = useCallback((contentNode: HTMLDivElement | null) => {
    if (!contentNode) return;

    const monthsNodesList = contentNode.querySelectorAll(`.${PickerLibClasses.month}`);
    const monthsNodes = Array.from(monthsNodesList);

    setMonthsNodes(monthsNodes);
  }, []);

  const handleRootNodeChange = useCallback(
    (node: HTMLDivElement | null) => {
      if (!node) return;

      setRootNode(node);
      updateMonthsNodes(node);
    },
    [updateMonthsNodes],
  );

  const loadingMonthsIndexes = useMemo(
    () =>
      getLoadingMonthsIndexes({
        loadingRanges,
        months,
        shownDate,
      }),
    [months, loadingRanges, shownDate],
  );

  const commonProps: Partial<LibDateRangePickerProps> = {
    ...rest,
    months,
    dayDisplayFormat,
    ranges: [selectionRange],
    color: customColor,
    rangeColors: [customColor], // Text color, selected block, focused inputs etc.
    showDateDisplay: false, // "From" and "To" fields
    ariaLabels: ariaLabels as AriaLabelsShape, // lib has wrong types here, according to demo
    classNames: PickerLibClasses,
    direction: finalDirection,
    locale: finalLocale,
    focusedRange,
    onChange: handleSelect,
    navigatorRenderer,
    dayContentRenderer,
  };

  useEffect(() => {
    onShownDateChange?.(shownDate);
    updateMonthsNodes(rootNode);
  }, [shownDate, rootNode, onShownDateChange, updateMonthsNodes]);

  return (
    <StyledRoot
      ref={handleRootNodeChange}
      direction={finalDirection}
      className={clsx(PickerClasses.picker, className)}
      sx={sx}
    >
      <LibDateRange {...commonProps} ref={disableSelectedMonthsResetOnPropsChange} />
      {monthsNodes.map((monthNode, index) => {
        // We use portal to insert loader inside month node
        const isLoading = loadingMonthsIndexes.includes(index);
        return createPortal(
          isLoading ? (
            <Box className={PickerClasses.loaderWrapper}>
              <MonthLoaderSlot {...monthLoaderSlotProps} />
            </Box>
          ) : null,
          monthNode,
          String(index),
        );
      })}
    </StyledRoot>
  );
};
