import { ComponentType, FC, useMemo } from 'react';

import { Box, Typography } from '@mui/material';

import { ClearButton } from './components/ClearButton';
import { CloseButton } from './components/CloseButton';
import StyledRoot, { FooterClasses } from './StyledFooter';
import { Action, FooterProps } from './types';

import { useDateRangePickerContext } from '../../context';

const componentsByActions: Record<Action, ComponentType> = {
  clear: ClearButton,
  close: CloseButton,
};

const defaultActions: FooterProps['actions'] = (params) => {
  const { view } = params;
  return view === 'modal' ? ['clear'] : ['clear', 'close'];
};

export const Footer: FC<FooterProps> = (props) => {
  const { subtitle, actions = defaultActions } = props;
  const context = useDateRangePickerContext();

  const finalActions = useMemo(() => {
    return Array.isArray(actions) ? actions : actions(context);
  }, [actions, context]);

  return (
    <StyledRoot className={FooterClasses.root}>
      <Box>
        {subtitle && (
          <Typography variant="body2" className={FooterClasses.subtitle}>
            {subtitle}
          </Typography>
        )}
      </Box>
      <Box className={FooterClasses.actions}>
        {finalActions.map((action, index) => {
          const Component = componentsByActions[action];
          return <Component key={index} />;
        })}
      </Box>
    </StyledRoot>
  );
};
