import { Box, generateUtilityClasses } from '@mui/material';
import { styled } from 'theme/index';

const PREFIX = 'DateRangePickerLayout';

export const LayoutClasses = generateUtilityClasses(PREFIX, ['root', 'modalView', 'popperView']);

export default styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`&.${LayoutClasses.root}`]: {
      background: theme.palette.common.white,
    },
    [`&.${LayoutClasses.modalView}`]: {
      padding: theme.spacing(0, 2),
    },
    [`&.${LayoutClasses.popperView}`]: {
      padding: theme.spacing(2),
    },
  };
});
