import { FC } from 'react';

import { Button, DialogActions, DialogContent, DialogTitle } from '@mui/material';
import { useTranslate } from 'hooks/index';
import { useDateRangePickerContext } from 'views/DateRangePickerInputView/context';

import { APPLY_BUTTON_TEST_ID, CANCEL_BUTTON_TEST_ID } from './constants';
import StyledDialog from './StyledModal';
import { ModalProps } from './types';

export const Modal: FC<ModalProps> = (props) => {
  const { label, content, ...dialogProps } = props;

  const { accept, cancel } = useDateRangePickerContext();

  const translate = useTranslate();

  return (
    <StyledDialog {...dialogProps}>
      <DialogTitle component="h4">{label}</DialogTitle>
      <DialogContent>{content}</DialogContent>
      <DialogActions sx={{ padding: 2 }}>
        <Button
          variant="outlined"
          sx={{ flexBasis: '50%' }}
          onClick={cancel}
          data-testid={CANCEL_BUTTON_TEST_ID}
        >
          {translate.root('buttons.cancel')}
        </Button>
        <Button
          variant="contained"
          sx={{ flexBasis: '50%' }}
          onClick={accept}
          data-testid={APPLY_BUTTON_TEST_ID}
        >
          {translate.root('buttons.apply')}
        </Button>
      </DialogActions>
    </StyledDialog>
  );
};
