import { createContext, useContext } from 'react';

import { noop } from 'lodash';

import { Value } from './components/Picker';
import { DateRangePickerInputViewProps } from './types';

export interface DateRangePickerContextValue {
  value: Value;
  isOpened: boolean;
  setValue: (value: Value) => void;
  resetValue: () => void;
  open: () => void;
  close: () => void;
  accept: () => void;
  cancel: () => void;
  view: DateRangePickerInputViewProps['view'];
}

const DateRangePickerContext = createContext<DateRangePickerContextValue>({
  // TODO: [RM-7750] add other props that should be accessable by slots and refactor the flow to avoid prop drilling
  value: { startDate: undefined, endDate: undefined },
  isOpened: false,
  view: 'popper',
  setValue: noop,
  resetValue: noop,
  open: noop,
  close: noop,
  accept: noop,
  cancel: noop,
});

export const DateRangePickerContextProvider = DateRangePickerContext.Provider;

export const useDateRangePickerContext = () => useContext(DateRangePickerContext);
