import { FC } from 'react';

import { Button, ButtonProps, generateUtilityClasses } from '@mui/material';
import { clsx } from 'clsx';
import { useTranslate } from 'hooks/index';
import { styled } from 'theme/index';
import { useDateRangePickerContext } from 'views/DateRangePickerInputView/context';

const PREFIX = 'DateRangePickerClearButton';

export const ClearButtonClasses = generateUtilityClasses(PREFIX, ['root']);

const StyledButton = styled(Button, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(() => {
  return {
    [`&.${ClearButtonClasses.root}`]: {
      whiteSpace: 'nowrap',
      textDecoration: 'underline',
    },
  };
});

export type ClearButtonProps = ButtonProps;

export const ClearButton: FC<ClearButtonProps> = (props) => {
  const { className, ...rest } = props;

  const translate = useTranslate();

  const { resetValue } = useDateRangePickerContext();

  return (
    <StyledButton
      variant="text"
      className={clsx(className, ClearButtonClasses.root)}
      onClick={resetValue}
      {...rest}
    >
      {translate.root('buttons.clear_dates')}
    </StyledButton>
  );
};
