import { FC, ReactNode, useCallback } from 'react';

import { IconButton, Typography, useTheme } from '@mui/material';
import { clsx } from 'clsx';
import { IcoMoon, IcoMoonProps } from 'custom/IcoMoon';
import { differenceInCalendarMonths, format } from 'date-fns';

import StyledRoot, { NavigationClasses } from './StyledNavigation';
import { NavigationProps } from './types';

export const LeftArrowIcon: FC<Partial<IcoMoonProps>> = (props) => {
  const { sx, ...rest } = props;

  const theme = useTheme();

  const isRtl = theme.direction === 'rtl';

  return (
    <IcoMoon
      {...rest}
      icon="expand-more"
      sx={{ transform: isRtl ? 'rotate(270deg)' : 'rotate(90deg)', ...sx }}
    />
  );
};

export const RightArrowIcon: FC<Partial<IcoMoonProps>> = (props) => {
  const { sx, ...rest } = props;

  const theme = useTheme();

  const isRtl = theme.direction === 'rtl';

  return (
    <IcoMoon
      {...rest}
      icon="expand-more"
      sx={{ transform: isRtl ? 'rotate(90deg)' : 'rotate(270deg)', ...sx }}
    />
  );
};

export const Navigation = <
  PrevIconButtonSlot extends typeof IconButton = typeof IconButton,
  NextIconButtonSlot extends typeof IconButton = typeof IconButton,
  LeftArrowIconSlot extends typeof LeftArrowIcon = typeof LeftArrowIcon,
  RightArrowIconSlot extends typeof RightArrowIcon = typeof RightArrowIcon,
>(
  props: NavigationProps<
    PrevIconButtonSlot,
    NextIconButtonSlot,
    LeftArrowIconSlot,
    RightArrowIconSlot
  >,
): ReactNode => {
  const { currentFocusedDate, changeShownDate, calendarProps, className, sx, slots, slotProps } =
    props;

  const {
    previousIconButton: PreviousIconButton = IconButton,
    nextIconButton: NextIconButton = IconButton,
    leftArrowIcon: LeftArrowIconSlot = LeftArrowIcon,
    rightArrowIcon: RightArrowIconSlot = RightArrowIcon,
  } = slots || {};

  const {
    previousIconButton: previousIconButtonProps,
    nextIconButton: nextIconButtonProps,
    leftArrowIcon: leftArrowIconProps,
    rightArrowIcon: rightArrowIconProps,
  } = slotProps || {};

  const { minDate, maxDate, months = 2, monthDisplayFormat, ariaLabels, direction } = calendarProps;

  const { prevButton: prevButtonAriaLabel, nextButton: nextButtonAriaLabel } = ariaLabels || {};

  const isPrevButtonDisabled = minDate
    ? differenceInCalendarMonths(currentFocusedDate, minDate) < 1
    : false;

  const isNextButtonDisabled = maxDate
    ? differenceInCalendarMonths(maxDate, currentFocusedDate) <= months - 1
    : false;

  const formattedCurrentMonth = format(currentFocusedDate, monthDisplayFormat || 'MMM yyyy');

  const isVerticalDirection = direction === 'vertical';

  const moveToPrevMonth = useCallback(() => {
    changeShownDate(-1, 'monthOffset');
  }, [changeShownDate]);

  const moveToNextMonth = useCallback(() => {
    changeShownDate(+1, 'monthOffset');
  }, [changeShownDate]);

  return (
    <StyledRoot className={clsx(NavigationClasses.root, className)} sx={sx} direction={direction}>
      <PreviousIconButton
        disabled={isPrevButtonDisabled}
        onClick={moveToPrevMonth}
        aria-label={prevButtonAriaLabel}
        {...previousIconButtonProps}
      >
        <LeftArrowIconSlot {...leftArrowIconProps} />
      </PreviousIconButton>
      {isVerticalDirection && (
        <Typography className={NavigationClasses.monthName}>{formattedCurrentMonth}</Typography>
      )}
      <NextIconButton
        disabled={isNextButtonDisabled}
        onClick={moveToNextMonth}
        aria-label={nextButtonAriaLabel}
        {...nextIconButtonProps}
      >
        <RightArrowIconSlot {...rightArrowIconProps} />
      </NextIconButton>
    </StyledRoot>
  );
};
