import { forwardRef } from 'react';

import { AutocompleteRenderInputParams, CircularProgress, TextField } from '@mui/material';
import { useTranslation } from 'next-i18next';
import { InputTemplate, InputTemplateProps } from 'templates/InputTemplate';

export interface FieldProps
  extends AutocompleteRenderInputParams,
    Partial<
      Pick<
        InputTemplateProps,
        | 'label'
        | 'withoutHelperTextSpace'
        | 'startIcon'
        | 'error'
        | 'helperText'
        | 'placeholder'
        | 'required'
      >
    > {
  loading?: boolean;
}

export const Field = forwardRef<HTMLInputElement, FieldProps>((props, ref) => {
  const {
    label,
    withoutHelperTextSpace,
    startIcon,
    error,
    helperText,
    placeholder,
    required,
    loading,
    ...renderInputParams
  } = props;

  const { id, InputProps, fullWidth } = renderInputParams;

  const { t } = useTranslation();

  return (
    <InputTemplate
      label={typeof label === 'string' ? t(label) : label}
      id={id}
      required={required}
      placeholder={placeholder}
      fullWidth={fullWidth}
      helperText={helperText}
      error={error}
      startIcon={startIcon}
      withoutHelperTextSpace={withoutHelperTextSpace}
    >
      {({ error, id, startAdornment, placeholder }) => {
        return (
          <TextField
            {...renderInputParams}
            inputRef={ref}
            error={error}
            id={id}
            placeholder={placeholder}
            variant="outlined"
            InputProps={{
              ...InputProps,
              startAdornment: (
                <>
                  {startAdornment}
                  {InputProps.startAdornment}
                </>
              ),
              endAdornment: (
                <>
                  {loading ? <CircularProgress color="inherit" size={20} /> : null}
                  {InputProps.endAdornment}
                </>
              ),
            }}
          />
        );
      }}
    </InputTemplate>
  );
});

Field.displayName = 'AutocompleteInputViewField';
