import { forwardRef } from 'react';

import { Box } from '@mui/material';
import { StyledRootProps } from '@packages/types';
import { clsx } from 'clsx';
import { format } from 'date-fns';

import { PickerDayClasses } from './StyledPickerDay';

export interface PickerDayProps extends StyledRootProps {
  date: Date;
  format?: string;
}

export const PickerDay = forwardRef<HTMLDivElement, PickerDayProps>((props, ref) => {
  const { date, className, format: dateFormat = 'd', ...rest } = props;

  return (
    <Box {...rest} ref={ref} className={clsx(className, PickerDayClasses.root)}>
      {format(date, dateFormat)}
    </Box>
  );
});

PickerDay.displayName = 'DateRangePickerDay';
