import { FC, MouseEvent, useCallback, useMemo } from 'react';

import { CircularProgress, MenuItem, MenuItemProps, Select, SelectProps } from '@mui/material';
import { useQuery } from '@tanstack/react-query';
import axios from 'axios';
import ResetIconButton from 'buttons/ResetIconButton';
import { IcoMoon } from 'custom/IcoMoon';
import { useIsRequiredInput } from 'hooks/useIsRequiredInput';
import { isEqual } from 'lodash';
import { useController, UseControllerProps } from 'react-hook-form';
import { InputTemplate, InputTemplateProps } from 'templates/InputTemplate';
import PlaceholderView from 'views/PlaceholderView';
import SeveralAdornments from 'views/SeveralAdornments';

export interface SelectFieldProps
  extends Partial<Omit<InputTemplateProps, 'value' | 'error'>>,
    Pick<SelectProps, 'startAdornment' | 'multiple'>,
    Pick<UseControllerProps, 'name'> {
  items?: Array<{ title: MenuItemProps['children']; value: MenuItemProps['value'] }>;
  selectProps?: Partial<SelectProps>;
  controllerProps?: Partial<UseControllerProps>;
  pathname?: string;
  resettable?: boolean;
}

const IconComponent: FC<{ className: string }> = (props) => {
  const { className } = props;

  return <IcoMoon icon="arrow-drop-down" className={className} />;
};

// TODO: replace with project fetchClient
const fetchClient = (params: { pathname: string; source: string }) => {
  const { pathname, source = '' } = params;

  return axios.get(`/${pathname}/${source}`);
};

export function SelectField(props: SelectFieldProps) {
  const {
    id,
    label,
    items = [],
    className,
    sx,
    helperText,
    name,
    pathname,
    controllerProps,
    selectProps = {},
    startIcon,
    multiple,
    disabled,
    required: requiredFromProps,
    resettable = true,
    ...rest
  } = props;

  const { required: requiredFromSelectProps } = selectProps;

  const resetValue = useMemo(() => (multiple ? [] : ''), [multiple]);

  const finalControllerProps = {
    name,
    ...controllerProps,
  };

  const {
    field: { value = resetValue, onChange, ref },
    fieldState: { error },
  } = useController(finalControllerProps);

  const isInputRequired = useIsRequiredInput(finalControllerProps);
  const isRequired = isInputRequired || requiredFromProps || requiredFromSelectProps;

  const isValueEmpty = isEqual(value, resetValue);

  const fetchedOptions = !!pathname;

  const fetchData = useCallback(async () => {
    if (!pathname) return [];
    const {
      data: { data },
    } = await fetchClient({ pathname, source: name });

    return Array.isArray(data) ? data : [];
  }, [name, pathname]);

  const { data, isLoading } = useQuery<unknown>({
    queryKey: [pathname, name],
    queryFn: fetchData,
    enabled: fetchedOptions,
    cacheTime: 15 * 1000,
    initialData: [],
  });

  const finalItems = pathname && Array.isArray(data) ? data : items;

  const handleResetClick = useCallback(
    (e: MouseEvent) => {
      e.stopPropagation();
      onChange(resetValue);
    },
    [resetValue, onChange],
  );

  return (
    <InputTemplate
      id={id || ''}
      label={label}
      required={isRequired}
      className={className}
      sx={sx}
      error={Boolean(error)}
      helperText={error?.message || helperText}
      value={value}
      startIcon={startIcon}
      disabled={disabled}
      {...rest}
    >
      {(inputProps) => {
        // Select doesn't support placeholder, but we can do it this way
        const renderValue = (selected: unknown) => {
          if (selected === resetValue) {
            return <PlaceholderView disabled={disabled}>{inputProps.placeholder}</PlaceholderView>;
          }

          if (Array.isArray(selected)) {
            if (!selected.length) {
              return (
                <PlaceholderView disabled={disabled}>{inputProps.placeholder}</PlaceholderView>
              );
            }
            return selected.join(', ');
          }
          return String(selected);
        };

        return (
          <Select
            displayEmpty
            renderValue={renderValue}
            id={inputProps.id}
            value={value}
            placeholder={inputProps.placeholder}
            onChange={onChange}
            fullWidth
            ref={ref}
            startAdornment={inputProps.startAdornment}
            IconComponent={IconComponent}
            endAdornment={
              <SeveralAdornments sx={{ position: 'absolute', right: 36 }}>
                {isLoading ? (
                  <CircularProgress color="inherit" size={20} />
                ) : (
                  !isValueEmpty &&
                  resettable && <ResetIconButton onClick={handleResetClick} disabled={disabled} />
                )}
              </SeveralAdornments>
            }
            error={inputProps.error}
            multiple={multiple}
            {...selectProps}
          >
            {finalItems.map((item) => {
              const { title, value } = item;
              return (
                <MenuItem value={value} key={JSON.stringify(title) + JSON.stringify(value)}>
                  {title}
                </MenuItem>
              );
            })}
          </Select>
        );
      }}
    </InputTemplate>
  );
}

export default SelectField;
