import { FC } from 'react';

import DateRangePickerInputView from 'components/views/DateRangePickerInputView';

import { useDateRangePickerInput } from './hooks/useDateRangePickerInput';
import { DateRangePickerInputProps } from './types';

const DateRangePickerInput: FC<DateRangePickerInputProps> = (props) => {
  const { fromName, toName, saveFormat, defaultValue, required: requriedProp, ...rest } = props;

  const { value, error, required, onBlur, onAccept } = useDateRangePickerInput({
    fromName,
    toName,
    saveFormat,
    defaultValue,
    required: requriedProp,
  });

  return (
    <DateRangePickerInputView
      {...rest}
      value={value}
      required={required}
      onAccept={onAccept}
      error={!!error}
      helperText={error}
      slotProps={{
        field: {
          slotProps: {
            field: { onBlur },
          },
        },
      }}
    />
  );
};

export default DateRangePickerInput;
