import { FC } from 'react';

import { Button, ButtonProps, generateUtilityClasses } from '@mui/material';
import { clsx } from 'clsx';
import { useTranslate } from 'hooks/index';
import { styled } from 'theme/index';
import { useDateRangePickerContext } from 'views/DateRangePickerInputView/context';

const PREFIX = 'DateRangePickerCloseButton';

export const CloseButtonClasses = generateUtilityClasses(PREFIX, ['root']);

const StyledButton = styled(Button, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(() => {
  return {
    [`&.${CloseButtonClasses.root}`]: {
      whiteSpace: 'nowrap',
    },
  };
});

export type CloseButtonProps = ButtonProps;

export const CloseButton: FC<CloseButtonProps> = (props) => {
  const { className, ...rest } = props;

  const translate = useTranslate();

  const { accept } = useDateRangePickerContext();

  return (
    <StyledButton
      variant="contained"
      className={clsx(className, CloseButtonClasses.root)}
      onClick={accept}
      {...rest}
    >
      {translate.root('buttons.close')}
    </StyledButton>
  );
};
