import { DateRangePickerProps as LibDateRangePickerProps, RangeFocus } from 'react-date-range';

import { PickerProps, Value } from '../../types';

export interface GetFocusedRangeParams extends Pick<PickerProps, 'focusedDateBehavior'> {
  startDate: Value['startDate'];
  endDate: Value['endDate'];
}

export type GetFocusedRangeReturn = NonNullable<LibDateRangePickerProps['focusedRange']>;

export const getFocusedRange = (params: GetFocusedRangeParams): GetFocusedRangeReturn => {
  const { startDate, endDate, focusedDateBehavior } = params;

  const startFocusedRange: RangeFocus = [0, 0];
  const endFocusedRange: RangeFocus = [0, 1];

  if (focusedDateBehavior) {
    return focusedDateBehavior === 'start' ? startFocusedRange : endFocusedRange;
  }

  if (startDate && endDate) return startFocusedRange;

  if (startDate && !endDate) return endFocusedRange;

  if (!startDate && endDate) return startFocusedRange;

  return startFocusedRange;
};
