import { MouseEvent, ReactNode, useCallback } from 'react';

import { InputAdornment, TextField } from '@mui/material';
import ResetIconButton from 'buttons/ResetIconButton';
import { clsx } from 'clsx';
import { IcoMoon } from 'custom/IcoMoon';
import { useDateRangePickerContext } from 'views/DateRangePickerInputView/context';
import SeveralAdornments from 'views/SeveralAdornments';

import StyledInputTemplate, { FieldClasses } from './StyledField';
import { FieldProps } from './types';

export const Field = <FieldSlot extends typeof TextField>(
  props: FieldProps<FieldSlot>,
): ReactNode => {
  const {
    slots,
    slotProps,
    isActive,
    label = '',
    startIcon = <IcoMoon icon="calendar" />,
    error = false,
    helperText = null,
    ...rest
  } = props;

  const { disabled } = rest;

  const { field: FieldSlot = TextField } = slots || {};

  const { field: fieldSlotProps } = slotProps || {};

  const {
    className: fieldClassName,
    inputProps,
    InputProps,
    disabled: fieldDisabled,
    ...restFieldSlotProps
  } = fieldSlotProps || {};

  const { resetValue } = useDateRangePickerContext();

  const handleResetClick = useCallback(
    (e: MouseEvent) => {
      // Stop propagation to not to receive field click event
      e.stopPropagation();
      resetValue();
    },
    [resetValue],
  );

  return (
    <StyledInputTemplate
      label={label}
      startIcon={startIcon}
      error={error}
      helperText={helperText}
      {...rest}
    >
      {({ startAdornment, ...restTemplateProps }) => (
        <FieldSlot
          {...restTemplateProps}
          className={clsx(fieldClassName, FieldClasses.field, {
            [FieldClasses.activeField]: isActive,
          })}
          inputProps={{ readOnly: true, ...inputProps }}
          InputProps={{
            startAdornment,
            endAdornment: (
              <InputAdornment position="end">
                <SeveralAdornments>
                  <ResetIconButton
                    className={FieldClasses.resetButton}
                    disabled={disabled || fieldDisabled}
                    onClick={handleResetClick}
                  />
                </SeveralAdornments>
              </InputAdornment>
            ),
            ...InputProps,
          }}
          disabled={disabled || fieldDisabled}
          {...restFieldSlotProps}
        />
      )}
    </StyledInputTemplate>
  );
};
