import { Box, generateUtilityClasses } from '@mui/material';
import { DateRangePickerProps as LibDateRangePickerProps } from 'react-date-range';
import { styled } from 'theme/index';

const PREFIX = 'DateRangePickerNavigation';

export const NavigationClasses = generateUtilityClasses(PREFIX, ['root', 'monthName']);

export default styled(Box, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
  shouldForwardProp: (p) => {
    return p !== 'direction';
  },
})<{ direction: LibDateRangePickerProps['direction'] }>(({ theme, direction }) => {
  const isVerticalDirection = direction === 'vertical';

  return {
    [`&.${NavigationClasses.root}`]: {
      height: 30,
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
      ...(isVerticalDirection
        ? { marginBottom: theme.spacing(1) }
        : { position: 'absolute', top: 0, left: 0, right: 0, zIndex: 5 }),
    },
    [`& .${NavigationClasses.monthName}`]: {
      ...theme.typography.body2,
      color: theme.palette.text.primary,
    },
  };
});
