import { FC } from 'react';

import { CircularProgress, CircularProgressProps } from '@mui/material';
import { StyledRootProps, WithRootProps } from '@packages/types';
import { clsx } from 'clsx';

import StyledRoot, { MonthLoaderClasses } from './StyledMonthLoader';

export type MonthLoaderProps = WithRootProps<Partial<CircularProgressProps>, StyledRootProps>;

export const MonthLoader: FC<MonthLoaderProps> = (props) => {
  const { sx, className, ...rest } = props;

  return (
    <StyledRoot className={clsx(MonthLoaderClasses.root, className)} sx={sx}>
      <CircularProgress size={32} className={MonthLoaderClasses.loader} {...rest} />
    </StyledRoot>
  );
};
