import { addDays } from 'date-fns';
import { max, min } from 'lodash';
import { DateRangePickerInputViewProps } from 'views/DateRangePickerInputView';

export interface GetMinMaxStayDatesRestrictionsParams
  extends Pick<DateRangePickerInputViewProps, 'minDate' | 'maxDate'> {
  selectedRange: [
    DateRangePickerInputViewProps['value']['startDate'],
    DateRangePickerInputViewProps['value']['endDate'],
  ];
  minStay?: number | null;
  maxStay?: number | null;
}

export type GetMinMaxStayDatesRestrictionsReturn = Pick<
  DateRangePickerInputViewProps,
  'minDate' | 'maxDate'
>;

export const getMinMaxStayDatesRestrictions = (
  params: GetMinMaxStayDatesRestrictionsParams,
): GetMinMaxStayDatesRestrictionsReturn => {
  const {
    selectedRange,
    minStay = 0,
    maxStay = 0,
    minDate: defaultMinDate,
    maxDate: defaultMaxDate,
  } = params;

  const [startDate, endDate] = selectedRange;

  const isOnlyStartDateSelected = startDate && !endDate;
  const shouldRestrictMinDate =
    isOnlyStartDateSelected && minStay && minStay >= 1 && isFinite(minStay);
  const shouldRestrictMaxDate =
    isOnlyStartDateSelected && maxStay && maxStay >= 1 && isFinite(maxStay);

  const minStayMinDate = shouldRestrictMinDate ? addDays(startDate, minStay) : undefined;
  const maxStayMaxDate = shouldRestrictMaxDate ? addDays(startDate, maxStay) : undefined;

  const minDate = max([defaultMinDate, minStayMinDate]);
  const maxDate = min([defaultMaxDate, maxStayMaxDate]);

  return {
    minDate,
    maxDate,
  };
};
