import { FC } from 'react';

import { BoxProps } from '@mui/material';
import { clsx } from 'clsx';
import { useDateRangePickerContext } from 'views/DateRangePickerInputView/context';

import StyledRoot, { LayoutClasses } from './StyledLayout';

export type LayoutProps = BoxProps;

export const Layout: FC<LayoutProps> = (props) => {
  const { className, ...rest } = props;

  const { view } = useDateRangePickerContext();

  return (
    <StyledRoot
      {...rest}
      className={clsx(className, LayoutClasses.root, {
        [LayoutClasses.modalView]: view === 'modal',
        [LayoutClasses.popperView]: view === 'popper',
      })}
    />
  );
};
