import { Children, FC } from 'react';

import { Divider } from '@mui/material';
import clsx from 'clsx';

import StyledRoot, { SeveralAdornmentsClasses } from './StyledSeveralAdornments';
import { SeveralAdornmentsProps } from './types';

const SeveralAdornments: FC<SeveralAdornmentsProps> = (props) => {
  const { children, className, dividerProps, ...rest } = props;
  const { className: dividerClassName, ...dividerRestProps } = dividerProps || {};

  return (
    <StyledRoot
      className={clsx(SeveralAdornmentsClasses.root, className)}
      alignItems="center"
      direction="row"
      divider={
        <Divider
          className={clsx(SeveralAdornmentsClasses.divider, dividerClassName)}
          orientation="vertical"
          flexItem
          {...dividerRestProps}
        />
      }
      spacing={1}
      {...rest}
    >
      {Children.map(children, (child) => {
        return child;
      })}
    </StyledRoot>
  );
};

export default SeveralAdornments;
