import { generateUtilityClasses, inputAdornmentClasses, outlinedInputClasses } from '@mui/material';
import { InputTemplate } from 'templates/InputTemplate';
import { styled } from 'theme/index';
import { SeveralAdornmentsClasses } from 'views/SeveralAdornments';

const PREFIX = 'DateRangePickerField';

export const FieldClasses = generateUtilityClasses(PREFIX, ['field', 'activeField', 'resetButton']);

export default styled(InputTemplate, {
  name: `Styled${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(({ theme }) => {
  return {
    [`& .${FieldClasses.field}`]: {
      width: '100%',

      '&:hover': {
        [`& .${FieldClasses.resetButton}`]: {
          visibility: 'visible',
          [`& + .${SeveralAdornmentsClasses.divider}`]: {
            visibility: 'visible',
          },
        },
      },
    },
    [`& .${SeveralAdornmentsClasses.root}`]: {
      position: 'relative',
    },
    [`& .${FieldClasses.resetButton}`]: {
      visibility: 'hidden',
      position: 'absolute',
      transform: 'translateX(-100%)',
      background: theme.palette.common.white,
      [`& + .${SeveralAdornmentsClasses.divider}`]: {
        visibility: 'hidden',
      },
      '&:hover': {
        background: theme.palette.actions.background,
      },
    },
    [`& .${FieldClasses.activeField}`]: {
      [`& .${inputAdornmentClasses.positionStart}`]: {
        color: theme.palette.primary.main,
      },
      [`& .${outlinedInputClasses.root}`]: {
        [`& .${outlinedInputClasses.notchedOutline}`]: {
          border: `1px solid ${theme.palette.primary.main}`,
        },
        '&:hover': {
          [`& .${outlinedInputClasses.notchedOutline}`]: {
            border: `1px solid ${theme.palette.primary.main}`,
          },
        },
      },
    },
  };
});
