import { ReactNode, useCallback, useEffect, useRef, useState } from 'react';

import { Box, ClickAwayListener, TextFieldProps, useMediaQuery, useTheme } from '@mui/material';
import { getFormattedDateRange } from 'helpers/getFormattedDateRange';
import { useTranslate } from 'hooks/useTranslate';
import { isEqual } from 'lodash';
import { ITheme } from 'theme/types';

import { Field } from './components/Field';
import { Footer } from './components/Footer';
import { Layout } from './components/Layout';
import { Modal } from './components/Modal';
import { Picker } from './components/Picker';
import { Popper } from './components/Popper';
import { DateRangePickerContextProvider } from './context';
import getFormattedToolbarLabel from './helpers/getFormattedToolbarLabel';
import { DateRangePickerInputViewProps } from './types';

const DateRangePickerInputView = <
  PickerSlot extends typeof Picker = typeof Picker,
  FooterSlot extends typeof Footer = typeof Footer,
  LayoutSlot extends typeof Layout = typeof Layout,
  FieldSlot extends typeof Field = typeof Field,
  ModalSlot extends typeof Modal = typeof Modal,
  PopperSlot extends typeof Popper = typeof Popper,
>(
  props: DateRangePickerInputViewProps<
    PickerSlot,
    FooterSlot,
    LayoutSlot,
    FieldSlot,
    ModalSlot,
    PopperSlot
  >,
): ReactNode => {
  const {
    sx,
    className,
    value: valueProp,
    focusedDateBehavior,
    separator = ' - ',
    ariaLabels,
    dateDisplayFormat = 'dd MMM yyyy',
    toolbarFormat = 'EEE, MMM d', // MUI like
    monthDisplayFormat,
    showMonthAndYearPickers = false,
    view,
    closeOnSelect,
    direction,
    color,
    months = 2,
    withoutHelperTextSpace,
    required,
    disabled,
    loadingRanges = [],
    error = false,
    helperText,
    label,
    dayDisplayFormat,
    disabledDates,
    disabledDay,
    id,
    locale,
    maxDate,
    minDate,
    placeholder,
    weekStartsOn,
    slotProps,
    slots,
    weekdayDisplayFormat,
    onShownDateChange,
    onChange,
    onOpen,
    onClose,
    onAccept,
    open: openProp = false,
  } = props;

  const translate = useTranslate();

  const {
    picker: PickerSlot = Picker,
    field: FieldSlot = Field,
    footer: FooterSlot = Footer,
    layout: LayoutSlot = Layout,
    modal: ModalSlot = Modal,
    popper: PopperSlot = Popper,
  } = slots || {};

  const {
    picker: pickerSlotProps,
    field: fieldSlotProps,
    footer: footerSlotProps,
    layout: layoutSlotProps,
    modal: modalSlotProps,
    popper: popperSlotProps,
  } = slotProps || {};

  const [anchorEl, setAnchorEl] = useState<Element | null>(null);
  const [open, setOpen] = useState(openProp);

  const [value, setValue] = useState<DateRangePickerInputViewProps['value']>(valueProp);
  const { startDate, endDate } = value;

  const prevAcceptedValue = useRef<DateRangePickerInputViewProps['value']>(value);

  useEffect(() => {
    setValue((prevValue) => (isEqual(prevValue, valueProp) ? prevValue : valueProp));
    prevAcceptedValue.current = valueProp;
  }, [valueProp]);

  useEffect(() => {
    setOpen(openProp);
  }, [openProp]);

  const theme = useTheme<ITheme>();

  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const isViewPassed = view !== undefined;
  const responsiveView: DateRangePickerInputViewProps['view'] = isMobile ? 'modal' : 'popper';

  const finalView = isViewPassed ? view : responsiveView;
  const isModal = finalView === 'modal';

  const finalMonths = isModal ? 1 : months;

  const isCloseOnSelectPassed = closeOnSelect !== undefined;
  const finalCloseOnSelect = isCloseOnSelectPassed ? closeOnSelect : !isModal;

  const toolbarLabel = getFormattedToolbarLabel({
    startDate,
    endDate,
    separator,
    format: toolbarFormat,
  });

  const handleChange = useCallback(
    (value: DateRangePickerInputViewProps['value']) => {
      if (disabled) return;
      setValue(value);
      onChange?.(value);
    },
    [disabled, onChange],
  );

  const handleOpen = useCallback(() => {
    if (open) return;
    setOpen(true);
    onOpen?.();
  }, [open, onOpen]);

  const handleClose = useCallback(() => {
    if (!open) return;
    setOpen(false);
    onClose?.();
  }, [open, onClose]);

  const handleAccept = useCallback(() => {
    if (disabled) return;
    handleClose();
    onAccept?.(value);
    prevAcceptedValue.current = value;
  }, [value, disabled, handleClose, onAccept]);

  const handleCancel = useCallback(() => {
    handleChange(prevAcceptedValue.current);
    handleClose();
  }, [handleChange, handleClose]);

  const handleReset = useCallback(() => {
    const emptyValue = {
      startDate: undefined,
      endDate: undefined,
    };

    handleChange(emptyValue);
    onAccept?.(emptyValue);
  }, [handleChange, onAccept]);

  const handlePopperClickAway = useCallback(() => {
    handleAccept();
  }, [handleAccept]);

  const handleDialogClickAway = useCallback(() => {
    handleCancel();
  }, [handleCancel]);

  const handleTextFieldClick = useCallback<Exclude<TextFieldProps['onClick'], undefined>>(() => {
    if (disabled) return;

    if (open) {
      handleAccept();
      return;
    }
    handleOpen();
  }, [open, disabled, handleOpen, handleAccept]);

  useEffect(() => {
    const { startDate: prevStartDate, endDate: prevEndDate } = prevAcceptedValue.current;
    const isFullRangeSelected = startDate && endDate;

    const isNewlySelectedRange = !isEqual([startDate, endDate], [prevStartDate, prevEndDate]);

    const shouldCloseAutomatically =
      finalCloseOnSelect && open && isFullRangeSelected && isNewlySelectedRange;

    if (shouldCloseAutomatically) {
      handleAccept();
    }
  }, [open, startDate, endDate, finalCloseOnSelect, handleAccept]);

  const formattedFieldValue = getFormattedDateRange({
    startDate,
    endDate,
    separator,
    dateDisplayFormat,
  });

  const content = (
    <LayoutSlot {...layoutSlotProps}>
      <PickerSlot
        value={value}
        onChange={handleChange}
        ariaLabels={ariaLabels}
        color={color}
        dateDisplayFormat={dateDisplayFormat}
        dayDisplayFormat={dayDisplayFormat}
        direction={direction}
        disabledDates={disabledDates}
        disabledDay={disabledDay}
        focusedDateBehavior={focusedDateBehavior}
        loadingRanges={loadingRanges}
        locale={locale}
        maxDate={maxDate}
        minDate={minDate}
        monthDisplayFormat={monthDisplayFormat}
        months={finalMonths}
        onShownDateChange={onShownDateChange}
        showMonthAndYearPickers={showMonthAndYearPickers}
        weekStartsOn={weekStartsOn}
        weekdayDisplayFormat={weekdayDisplayFormat}
        {...pickerSlotProps}
      />
      <FooterSlot {...footerSlotProps} />
    </LayoutSlot>
  );

  return (
    <DateRangePickerContextProvider
      value={{
        value,
        isOpened: open,
        setValue: handleChange,
        resetValue: handleReset,
        open: handleOpen,
        close: handleClose,
        accept: handleAccept,
        cancel: handleCancel,
        view: finalView,
      }}
    >
      <ClickAwayListener
        onClickAway={isModal ? handleDialogClickAway : handlePopperClickAway}
        mouseEvent={open ? 'onMouseDown' : false}
        touchEvent={open ? 'onTouchEnd' : false}
      >
        <Box sx={sx} className={className}>
          <FieldSlot
            id={id}
            isActive={open}
            label={label ?? translate.root('inputs.dateRangePickerInput.date_range')}
            error={error}
            disabled={disabled}
            helperText={helperText}
            withoutHelperTextSpace={withoutHelperTextSpace}
            placeholder={placeholder}
            required={required}
            {...fieldSlotProps}
            slotProps={{
              ...fieldSlotProps?.slotProps,
              field: {
                ref: setAnchorEl,
                value: formattedFieldValue,
                onClick: handleTextFieldClick,
                ...fieldSlotProps?.slotProps?.field,
              },
            }}
          />

          {isModal ? (
            <ModalSlot
              open={open}
              label={toolbarLabel}
              content={content}
              onClose={handleDialogClickAway}
              {...modalSlotProps}
            />
          ) : (
            <PopperSlot open={open} anchorEl={anchorEl} {...popperSlotProps}>
              {content}
            </PopperSlot>
          )}
        </Box>
      </ClickAwayListener>
    </DateRangePickerContextProvider>
  );
};

export default DateRangePickerInputView;
