import { FC } from 'react';

import { Popper as MuiPopper, PopperProps as MuiPopperProps, useTheme } from '@mui/material';
import { ITheme } from 'theme/index';

export type PopperProps = MuiPopperProps;

// we use popper modifier to have an offset from the input
const offsetModifier: NonNullable<MuiPopperProps['modifiers']>[number] = {
  name: 'offset',
  options: {
    offset: [0, 8],
  },
};

export const Popper: FC<PopperProps> = (props) => {
  const { modifiers = [], sx, ...rest } = props;

  const theme = useTheme<ITheme>();

  return (
    <MuiPopper
      {...rest}
      modifiers={[...modifiers, offsetModifier]}
      sx={{
        borderRadius: 2,
        overflow: 'hidden',
        border: `1px solid ${theme.palette.other.border}`,
        zIndex: theme.zIndex.tooltip,
        ...sx,
      }}
    />
  );
};
