import { addMonths, endOfMonth, isAfter, isBefore, startOfMonth } from 'date-fns';

interface GetLoadingMonthsIndexesParams {
  loadingRanges: Array<[Date, Date]>;
  months: number;
  shownDate: Date;
}

type GetLoadingMonthsIndexesReturn = number[];

export const getLoadingMonthsIndexes = (
  params: GetLoadingMonthsIndexesParams,
): GetLoadingMonthsIndexesReturn => {
  const { loadingRanges, months, shownDate } = params;

  const monthIndexes = [...Array(months).keys()];

  return monthIndexes.filter((monthIndex) => {
    const visibleFrom = startOfMonth(addMonths(shownDate, monthIndex));
    const visibleTo = endOfMonth(visibleFrom);

    const isMonthLoading = loadingRanges.some((range) => {
      const [loadingFrom, loadingTo] = range;

      return !isAfter(loadingFrom, visibleTo) && !isBefore(loadingTo, visibleFrom);
    });

    return isMonthLoading;
  });
};
