import { ComponentProps, ForwardedRef, forwardRef, useCallback } from 'react';

import {
  Autocomplete,
  AutocompleteProps,
  AutocompleteRenderInputParams,
  Chip,
  PopperProps,
} from '@mui/material';
import { IcoMoon } from 'custom/IcoMoon';

import { DropdownGroup } from './DropdownGroup';
import { Field as DefaultFieldSlot, FieldProps as DefaultFieldSlotProps } from './Field';

const popperOffsetModifier: NonNullable<PopperProps['modifiers']>[number] = {
  name: 'offset',
  enabled: true,
  options: {
    offset: [0, 4],
  },
};

interface Slots {
  field?: typeof DefaultFieldSlot;
}

type SlotProps = {
  [Key in keyof Slots]?: Partial<ComponentProps<NonNullable<Slots[Key]>>>;
};

export type AutocompleteInputViewProps<
  Option,
  Multiple extends boolean | undefined = undefined,
  DisableClearable extends boolean | undefined = undefined,
  FreeSolo extends boolean | undefined = undefined,
> = Omit<AutocompleteProps<Option, Multiple, DisableClearable, FreeSolo>, 'renderInput'> &
  Omit<DefaultFieldSlotProps, keyof AutocompleteRenderInputParams> & {
    slots?: Slots;
    slotProps?: SlotProps;
  };

const AutocompleteInputViewWithRef = <
  Option,
  Multiple extends boolean | undefined = undefined,
  DisableClearable extends boolean | undefined = undefined,
  FreeSolo extends boolean | undefined = undefined,
>(
  props: AutocompleteInputViewProps<Option, Multiple, DisableClearable, FreeSolo>,
  ref: ForwardedRef<HTMLInputElement>,
) => {
  const {
    label,
    startIcon,
    error,
    helperText,
    placeholder,
    required,
    withoutHelperTextSpace,
    loading,
    componentsProps,
    slots,
    slotProps,
    ...rest
  } = props;

  const { getOptionLabel } = rest;

  const { popper: popperComponentProps } = componentsProps || {};
  const { modifiers: popperModifiers = [] } = popperComponentProps || {};

  const { field: FieldSlot = DefaultFieldSlot } = slots || {};
  const { field: fieldSlotProps } = slotProps || {};

  const renderGroup = useCallback<
    NonNullable<
      AutocompleteInputViewProps<Option, Multiple, DisableClearable, FreeSolo>['renderGroup']
    >
  >((params) => {
    const { key, group, children } = params;

    return (
      <DropdownGroup key={key} title={group}>
        {children}
      </DropdownGroup>
    );
  }, []);

  const renderTags = useCallback<
    NonNullable<
      AutocompleteInputViewProps<Option, Multiple, DisableClearable, FreeSolo>['renderTags']
    >
  >(
    (value, getTagProps) => {
      return value.map((option, index) => {
        const { key, ...tagProps } = getTagProps({ index });
        const label = getOptionLabel?.(option);
        return <Chip key={key} {...tagProps} label={label} size="small" />;
      });
    },
    [getOptionLabel],
  );

  const renderInput = useCallback<
    NonNullable<AutocompleteProps<Option, Multiple, DisableClearable, FreeSolo>['renderInput']>
  >(
    (params) => {
      return (
        <FieldSlot
          ref={ref}
          label={label}
          withoutHelperTextSpace={withoutHelperTextSpace}
          startIcon={startIcon}
          error={error}
          helperText={helperText}
          placeholder={placeholder}
          required={required}
          {...params}
          {...fieldSlotProps}
        />
      );
    },
    [
      FieldSlot,
      error,
      helperText,
      label,
      placeholder,
      ref,
      required,
      startIcon,
      withoutHelperTextSpace,
      fieldSlotProps,
    ],
  );

  return (
    <Autocomplete
      blurOnSelect
      componentsProps={{
        ...componentsProps,
        popper: {
          ...popperComponentProps,
          modifiers: [...popperModifiers, popperOffsetModifier],
        },
      }}
      popupIcon={<IcoMoon icon="arrow-drop-down" />}
      renderGroup={renderGroup}
      renderTags={renderTags}
      renderInput={renderInput}
      {...rest}
    />
  );
};

export const AutocompleteInputView = forwardRef(AutocompleteInputViewWithRef) as <
  Option,
  Multiple extends boolean | undefined = undefined,
  DisableClearable extends boolean | undefined = undefined,
  FreeSolo extends boolean | undefined = undefined,
>(
  props: AutocompleteInputViewProps<Option, Multiple, DisableClearable, FreeSolo>,
) => ReturnType<typeof AutocompleteInputViewWithRef>;
